.intro {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 65px;
    height: calc(100vh - 65px);
    background-color: #7AA5B3;
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
}


.intro__container{
    display:flex;
    flex-direction: row; 
    justify-content: space-around;
    width: 100%;
    padding-bottom: 80px;
}

.intro__text{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 30%;
}
.intro__title{
    white-space: pre-wrap;
    color: white;
    font-size: 2em ;
    text-align: start;
}

.intro__subtitle{
    color: white;
    list-style: none;
    text-align: start;
    font-size: 1.2em ;
}

.intro__list{
    padding: 0;
    margin: 0;
}

.intro__button{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 320px;
    margin-top: 60px;
}

.intro__picture-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
}

.intro__picture{
    width: 300px;
    height: 400px
}


@media screen and (max-width: 1025px) {
    .intro {
    min-height: 616px;
    height: auto;
    background-size: 100% 80%;
   }
}

@media screen and (max-width: 800px) {
    .intro {
    min-height: 616px;
    height: auto;
        /* background-image: url(../../images/intro/webp-test-mobile_.webp); */
    background-size: 90% 90%;
    background-position-y: 20px;
    }    

   .intro__text {
    padding-top: 0px;
    width: 88%;
    margin-left: 10%;
   }

   .intro__mobile-container {
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
   }

   .intro__picture{
    display: block;
    width: 90%;
    /* height: 300px; */
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
    }

   .intro__title {
    margin-top: 0;
    text-align: start;
    background-size: 90% 70%;
   }

   .intro__buttons{
    /* margin-top: 230px; */
    margin-top: 0;
    max-width: 150px;
    }
    
    .intro__container{
    min-height: 600px;
    }

    .intro__subtitle{
    text-align: start;
    }

    .intro__text {
    /* position: absolute;
    top: 460px; */
    left: 10%;
    margin-left: 0;
    }
    .intro__button{
        flex-direction: column;
        align-items: flex-start;
        width: 100%; 
        height: 100px;
        margin-top: 30px;
    }
    .intro__container{
        display:flex;
        flex-direction: column; 
        justify-content: flex-start;
        margin-top: 35px;
        align-items: center;
        text-align: center;
        width: 100%;
    }
}

@media screen and (max-width: 500px) {
    .intro{
        height: 100%;
        padding-bottom: 50px;
    }
    .intro__title {
        margin-bottom: 20px;
        font-size: 24px;
        white-space: unset;
      }
    
    .intro__picture-container{
        width: 90%;
    }
    .intro__picture{
        width: 100%;
        margin: 0;
    }

    .intro__text {
        margin-left: 0px;
    }
    .intro__button{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 10px;
        height: 100%;   
    }
} 